.dropdown {
  border: 1px darkgray solid;
  cursor: pointer;
  width: 100%; }
  .dropdown__title {
    margin: 0;
    padding: 12px; }
  .dropdown__title-icon {
    position: absolute;
    right: 12px; }
    @media screen and (min-width: 768px) {
      .dropdown__title-icon {
        margin-left: 12px;
        position: initial; } }
  .dropdown__option {
    width: 100%;
    margin: 25px; }
    @media screen and (min-width: 768px) {
      .dropdown__option {
        margin: 20px; } }
  .dropdown__option-checkbox {
    position: absolute;
    opacity: 0; }
    .dropdown__option-checkbox + label {
      position: relative;
      cursor: pointer;
      display: -ms-inline-flexbox;
      display: inline-flex;
      padding: 0;
      white-space: nowrap; }
    .dropdown__option-checkbox + label:before {
      content: '';
      margin-right: 1em;
      display: inline-block;
      vertical-align: text-top;
      width: 1em;
      height: 1em;
      background: white;
      border: 1px darkgray solid; }
    .dropdown__option-checkbox:hover + label:before {
      border: 1px black solid; }
    .dropdown__option-checkbox:checked + label:before {
      background: #f099b0; }
    .dropdown__option-checkbox:disabled + label:before {
      -webkit-box-shadow: none;
      box-shadow: none;
      background: #e4e4e4; }
  .dropdown__options {
    display: block;
    position: absolute;
    left: 0;
    background-color: white;
    min-width: 100%;
    border: 1px darkgray solid;
    z-index: 1; }
    .dropdown__options--opened {
      border: 1px black solid; }
  .dropdown:hover {
    border: 1px black solid; }
  .dropdown--opened {
    border: 1px black solid; }



