.products {
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 80px;
  justify-items: center; }
  @media screen and (min-width: 768px) {
    .products {
      grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
      grid-row-gap: initial;
      grid-gap: 80px; } }



